import React, {useState} from 'react';
import {Button, Form, InputGroup} from "react-bootstrap";
import DisabledButton from "./DisabledButton";
import axios from "axios";

function SignupForm(props) {

    const inputBox = document.getElementById('email');
    const [mailVal, setMailVal] = useState("");

    const EmailAuth = () => {
        console.log('asd');
        if (!mailVal) {
            axios.post('http://localhost:8080/emailConfirm', null, {
                params: {
                    email: mailVal,
                }
            })
                .then(() => {
                   console.log('then 절')
                })
                .catch(() => {
                    console.log('catch 절')
                });
        }
    };

    return (
        <Form action="" id={'frm'}>
            <p className={'text-start mt-3'}><strong>이메일</strong></p>
            <InputGroup className="my-2">
                <InputGroup.Text id="basic-addon1"><i className="bi bi-envelope"></i></InputGroup.Text>
                <Form.Control
                    id={'email'}
                    placeholder="이메일을 입력해주세요"
                    aria-describedby="basic-addon1"
                    name={'personId'}
                    onChange={ e => {setMailVal(e.target.value)}}
                />
                <button type={'button'} className={'theme-outline-btn'} onClick={() => EmailAuth()}>이메일 인증</button>
            </InputGroup>
            <p className={'text-start mt-3'}><strong>별명</strong></p>
            <InputGroup className="my-2">
                <InputGroup.Text id="basic-addon1"><i className="bi bi-emoji-smile"></i></InputGroup.Text>
                <Form.Control
                    placeholder="별명을 입력해주세요"
                    aria-describedby="basic-addon1"
                    name={'personId'}
                />
            </InputGroup>
            <div className={'my-5'}>
                <p className={'text-start mb-2'}><strong>비밀번호</strong></p>
                <InputGroup className="">
                    <InputGroup.Text id="basic-addon2"><i className="bi bi-lock"></i></InputGroup.Text>
                    <Form.Control
                        type={'password'}
                        placeholder="영문자 + 숫자 포함 최소 8자"
                        aria-describedby="basic-addon2"
                        name={'personPassword'}
                    />
                </InputGroup>

                <p className={'text-start my-2'}><strong>비밀번호 확인</strong></p>
                <InputGroup className="">
                    <InputGroup.Text id="basic-addon2"><i className="bi bi-lock"></i></InputGroup.Text>
                    <Form.Control
                        type={'password'}
                        placeholder="비밀번호를 입력해주세요"
                        aria-describedby="basic-addon2"
                        name={'personPassword'}
                    />
                </InputGroup>
            </div>
            <div className={'d-grid my-5'}>
                <DisabledButton />
            </div>
        </Form>
    )
}

export default SignupForm;